if ($(window).width() <= 768) {
    $('#pc-activity-box').removeClass('swiper--pagination-hidden');
}

else {
    $('#pc-activity-box').addClass('swiper--pagination-hidden');
}

$(window).resize(function () {
    if ($(window).width() <= 768) {     
        $('#pc-activity-box').removeClass('swiper--pagination-hidden');
    }
    
    else {
        $('#pc-activity-box').addClass('swiper--pagination-hidden');
    }
});

var PCActivity = new Swiper('#pc-activity-box', {
    slidesPerView: 4,
    slidesPerGroup: 4,
    spaceBetween: 15,

    autoplay: {
        delay: 4500,
        disableOnInteraction: false,
    },

    pagination: {
        el: '#pc-activity-box .swiper-pagination',
        clickable: true,
    },
    
    breakpoints: {
        736: {
            slidesPerView: 2,
            slidesPerGroup: 2,
        },
    },
});